      layout="prev, pager, next"
      :total="total"
      @current-change="paginationChange"
      @prev-click="prevClick"
      @next-click="nextClick"
    ></el-pagination>
    <div class="pagination-sup">{{ pageSize }}/{{ total }}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pageSize: 10,
      total: 156,
    };
  },

  methods: {
    paginationChange(e) {
      this.$message.info(e);
      console.log("current改变", e);
    },
    prevClick() {
      console.log("上一页");
    },
    nextClick() {
      console.log("下一页");
    },
  },
};
</script>

